import React, { useState } from "react";
import { setup, styled } from "goober";

setup(React.createElement);

const HeartUI = styled('div')(({ click, styles }) => [
	{
		width: '100px',
		height: '100px',
		background: 'url("data:image/png;base64,") no-repeat',
		cursor: 'pointer',
		display: 'inline-block',
    margin: '-22px',
    marginTop: '-17px',
    zoom: '0.9'
	},
	(click == 1) && {
		backgroundPosition: '-2799px 2px',
		transition: 'background 0.15s steps(28)'
	},
	styles
]);

export const Heart = ({ onClick, onClickComplete, styles }) => {

  const [isClick, setIsClick] = useState(0);
  const [timer, setTimer] = useState();

	return (
    <HeartUI click={isClick} styles={styles} 
      onClick={() => {
        /**
         * set isClick to 1 to show animation over heart
         */
        setIsClick(1)

        /** 
         * onClick need to call everytime, but onClickComplete will call
         * only when click press stops within 1 second
         */ 
        onClick()
        
        /**
         * after 400 miliseconds reset the heart
         */ 
        setTimeout(() => {
          setIsClick(0)
        }, 150);

        // clear timeout in every click
        if (timer) {
          clearTimeout(timer);
        }

        /**
         * if after 1 second the value of isClick remain 0 means click finished
         */ 
        const timout = setTimeout(() => {
          if (isClick == 0) {
            onClickComplete()
          }
        }, 1500);
        // set in setTimer
        setTimer(timout)
      }}
    />
  )
}